import logoSvg from '../images/logo.svg';
import { Link, withRouter } from 'react-router-dom'
import React from 'react'

function Header(props) {
  return (
    <header className="header">
      <img src={logoSvg} alt="Логотип" className="header__logo" />
      {props.isLoggedIn
        ? <>
          <p className="header__email">{props.currentEmail}</p>
          <Link to="/sign-in" className="header__status" onClick={props.onSignOut}>Выйти</Link>
        </>
        : <Link to={props.history.location.pathname === "/sign-in"
          ? '/sign-up'
          : '/sign-in'
        } className="header__status" >{
          props.history.location.pathname === "/sign-in"
        ? 'Регистрация'
        : 'Войти'}</Link>
      }
    </header>
  )
}

export default withRouter(Header)